import {Component} from '@angular/core';
import {APIService} from '../../globals/services/API.service';
import {AuthService} from '../../globals/services/auth.service';
import {KivalasztottIngatlanokService} from '../../globals/services/kivalasztott-ingatlanok.service';
import {ModalComponent} from './modal/modal.component';
@Component({
    selector: 'szavazogomb',
    moduleId: module.id,
    templateUrl: 'szavazogomb.html',

    styleUrls: ["szavazogomb.css"],
    directives: [ModalComponent],
    inputs: ['gombStyle', 'ingatlanObject']

})
export class SzavazoGombComponent {
    gombStyle:string = 'nagy';
    ingatlanObject:any;
    szavazogomb=`<img width="55px" src="template/images/btn_licit.png">`;
   szavazogombNagy=` <button  type="button" class="btn btn-custom"> SORSJEGY VÁSÁRLÁSA</button>`;
    constructor(private _APIService:APIService, private _AuthService:AuthService,private _KivalasztottIngatlanokService:KivalasztottIngatlanokService) {

    }

    vasarlok() {

        console.log('vásárlás');

    }

    vasarlas_elfogadva(){
        var currentdate = new Date();
        let obj:any = {
            ingatlan_id: this.ingatlanObject.id,
            date: currentdate.getTime()

        }
        this._APIService.getResponse('api/licit/', obj).subscribe(
                data=> {
                this.ingatlanObject.szazalek_ertekesitve = data.return;
                this._KivalasztottIngatlanokService.setKuvalasztottIngatlanok(data.kivalasztott_ingatlanok);
                console.log(data);


            },
                error=> {
                console.log(error);
            })
    }
}
